import React, { useMemo } from 'react';
import { BarStack } from '@vx/shape';
import { Group } from '@vx/group';
import { AxisBottom, AxisLeft } from '@vx/axis';
import { scaleBand, scaleLinear, scaleOrdinal } from '@vx/scale';
import { withTooltip } from '@vx/tooltip';
import { LegendOrdinal } from '@vx/legend';
import { localPoint } from '@vx/event';
import mapValues from 'lodash/mapValues'
import get from 'lodash/get'
import { useImmer } from 'use-immer';
import { NodeGroup } from 'react-move'
import { easeExpInOut } from 'd3-ease'
import { format } from 'd3-format'

import useResponsive from '../../contexts/mediaQuery/useResponsive'
import Box from '../Box'
import Flex from '../Flex'
import Text from '../Text'
import Checkbox from '../Checkbox'
import TickLabel from './TickLabel'
import TooltipBox from './TooltipBox'

import theme from '../ThemeProvider/theme'

const p = format('.1%')

let tooltipTimeout;

const defaultMargin = {
  top: 3,
  left: 6,
  right: 5,
  bottom: 2,
}

export default withTooltip(
  ({
    data,
    xAccessor,
    keys,
    colors,
    width,
    height,
    margin,
    tooltipOpen,
    tooltipLeft,
    tooltipTop,
    tooltipData,
    hideTooltip,
    showTooltip,
    xTick = {},
    yTick = {},
    valueFormat = v => v,
    showSum,
    showSumButNotAll = true,
    hideLegned,
    showPercentOnTooltip,
    noTooltip,
  }) => {
    if (!data || !xAccessor || !keys || !colors) return null
    const x = typeof xAccessor === 'string' ? d => d[xAccessor] : xAccessor
    const { em } = useResponsive()
    if (isNaN(em)) return null
    const [activeKeys, updateActiveKeys] = useImmer(keys.reduce((ak, k) => {
      ak[k] = true
      return ak
    }, {}))
    const keysCount = Object.values(activeKeys).reduce((c, b) => c + b, 0)
    const onlyOneKey = keysCount === 1
    const allKeys = keysCount === keys.length
    const emedMargin = mapValues(Object.assign({}, defaultMargin, margin), d => d * em)
    // bounds
    const xMax = width - emedMargin.left - emedMargin.right;
    const yMax = height - emedMargin.top - emedMargin.bottom;

    const totals = data.reduce((ret, cur) => ret.concat(keys.reduce((t, k) => t + +cur[k], 0)), []);

    // scales
    const xScale = scaleBand({
      domain: data.map(x),
      padding: 0.5,
    });
    const yScale = scaleLinear({
      domain: [0, Math.max(...totals)],
      nice: true,
    });

    // const color = k => colors[k]
    const color = scaleOrdinal({
      domain: keys,
      range: colors,
    })
    xScale.rangeRound([0, xMax]);
    yScale.range([yMax, 0]);
    const keysReverse = Array.from(keys).reverse()
    const theKeys = keysReverse.filter(k => activeKeys[k])
    return (
      <Box position="relative">
        {useMemo(() => (
          <>
            <svg width={width} height={height}>
              <Group top={emedMargin.top} left={emedMargin.left}>
                <BarStack
                  data={data}
                  keys={theKeys}
                  x={x}
                  xScale={xScale}
                  yScale={yScale}
                  color={color}
                >
                  {barStacks => {
                    const keyed = barStacks.reduce((kd, bs) => {
                      kd[bs.key] = bs
                      return kd
                    }, {})
                    return keysReverse.map(key => {
                      const barStack = keyed[key]
                      return (
                        <NodeGroup
                          key={key}
                          data={barStack ? barStack.bars : []}
                          keyAccessor={b => b.index}
                          start={(d) => ({
                            height: 0,
                            y: d.y + d.height,
                          })}
                          enter={(d) => ({
                            height: [d.height],
                            y: [d.y],
                            timing: { duration: 750, ease: easeExpInOut },
                          })}
                          update={(d) => ({
                            height: [d.height],
                            y: [d.y],
                            timing: { duration: 750, ease: easeExpInOut },
                          })}
                          leave={(d) => ({
                            height: [0],
                            y: [d.y + d.height],
                            timing: { duration: 750, ease: easeExpInOut },
                          })}
                        >
                          {nodes => {
                            return (
                              <>
                                {nodes.map(({ data: bar, state }) => {
                                  return (
                                    <Group key={`bar-stack-${key}-${bar.index}`} left={bar.x} top={state.y}>
                                      <rect
                                        stroke="black"
                                        strokeWidth={1.25}
                                        height={state.height}
                                        width={bar.width}
                                        fill={bar.color}
                                        onMouseLeave={event => {
                                          tooltipTimeout = setTimeout(() => {
                                            hideTooltip();
                                          }, 300);
                                        }}
                                        onMouseMove={event => {
                                          if (tooltipTimeout) clearTimeout(tooltipTimeout);
                                          const point = localPoint(event)
                                          const top = point.y - emedMargin.top;
                                          const offset = xScale.paddingInner() * xScale.step() / 2;
                                          const left = bar.x + bar.width + offset;
                                          showTooltip({
                                            tooltipData: bar,
                                            tooltipTop: top,
                                            tooltipLeft: left
                                          });
                                        }}
                                      />
                                      {get(barStack, 'index') === keysCount - 1 && (onlyOneKey || showSum || (!allKeys && showSumButNotAll)) && (
                                        <text
                                          x={xScale.paddingInner() * bar.width}
                                          y="-0.5em"
                                          textAnchor="middle"
                                          fill={theme.colors.primary}
                                          fontSize="1.5em"
                                        >
                                          {valueFormat((showSum || showSumButNotAll) ? theKeys.reduce((s, k) => s + +bar.bar.data[k], 0) : bar.bar.data[bar.key])}
                                        </text>
                                      )}
                                    </Group>
                                  );
                                })}
                              </>
                            )
                          }}
                        </NodeGroup>
                      )
                    })
                  }}
                </BarStack>
              </Group>
              <Group left={emedMargin.left}>
                <AxisLeft
                  top={emedMargin.top}
                  left={0}
                  scale={yScale}
                  labelProps={{
                    fontSize: '1.125em',
                    fill: '#fff',
                    writingMode: 'tb',
                    transform: 'rotate(0)',
                    fontWeight: 'bold',
                    fontStretch: 'condensed',
                    letterSpacing: -8,
                    x: -em * 5.25,
                    y: 0,
                  }}
                  stroke="#fff"
                  strokeWidth={3}
                  tickStroke="#fff"
                  tickStrokeWidth={1.5}
                  tickLabelProps={() => ({
                    fill: '#fff',
                    textAnchor: 'end',
                    fontSize: '1.125em',
                    dx: '-0.25em',
                    dy: '0.25em'
                  })}
                  tickComponent={({ formattedValue, ...tickProps }) => (
                    <TickLabel {...tickProps}>{formattedValue}</TickLabel>
                  )}
                  {...yTick}
                />
                <AxisBottom
                  top={yMax + emedMargin.top}
                  strokeWidth={3}
                  stroke="#fff"
                  labelProps={{
                    fill: '#fff',
                    fontSize: '1.125em',
                    x: xMax,
                    dy: '-0.125em',
                    fontWeight: 'bold',
                  }}
                  scale={xScale}
                  hideTicks
                  tickLabelProps={() => ({
                    fill: '#fff',
                    fontSize: '1.25em',
                    textAnchor: 'middle',
                    dy: '0.25em',
                  })}
                  tickComponent={({ formattedValue, ...tickProps }) => (
                    <TickLabel {...tickProps}>{formattedValue}</TickLabel>
                  )}
                  {...xTick}
                />
              </Group>
            </svg>
            {!hideLegned && (
              <Box
                position="absolute"
                top={0}
                right={0}
              >
                <LegendOrdinal scale={color}>
                  {labels => (
                    <>
                      <Text color="white">可依類別點選</Text>
                      {labels.map(l => (
                        <Flex
                          key={l.datum}
                          is="label"
                          width={1}
                          my="1em"
                          cursor="pointer"
                        >
                          <Checkbox
                            fontSize="1.5em"
                            color="white"
                            name="active"
                            checked={activeKeys[l.datum]}
                            onChange={(e) => updateActiveKeys(draft => {
                              if (onlyOneKey && !e.target.checked) {
                                draft = mapValues(draft, () => true)
                              } else {
                                draft[l.datum] = e.target.checked;
                              }
                              return draft
                            })}
                          />
                          <Box
                            ml="0.5em"
                            flex="1"
                            bg={l.value}
                            color="black"
                            textAlign="center"
                            borderRadius="2em"
                            px="0.75em"
                            pt="0.25em"
                            userSelect="none"
                          >
                            {l.text}
                          </Box>
                        </Flex>
                      ))}
                    </>
                  )}
                </LegendOrdinal>
              </Box>
            )}
          </>
        ), [
          activeKeys,
          allKeys,
          em,
          height,
          keysCount,
          keysReverse,
          onlyOneKey,
          theKeys,
          width,
        ])}
        {tooltipOpen && !noTooltip && (
          <TooltipBox
            top={tooltipTop}
            left={tooltipLeft}
          >
            <Text fontWeight="bold" color={color(tooltipData.key)}>{tooltipData.key}</Text>
            <Text>
              {valueFormat(tooltipData.bar.data[tooltipData.key])}
              {showPercentOnTooltip && ` (${p(tooltipData.bar.data[tooltipData.key] / tooltipData.bar.data.sum)})`}
            </Text>
          </TooltipBox>
        )}
      </Box>
    );
  }
);
